<script>
import { getFansList } from '@/api/fans.js'
import echarts from 'echarts'
import barDemo from './components/bar-demo.vue'
import mapDemo from './components/map-demo'
import bmapDemo from './components/bmap-demo'
export default {
  name: 'FansIndex',
  components: {
    barDemo,
    mapDemo,
    bmapDemo
  },
  data () {
    return {
      activeName: 'first',
      fansList: [],
      pageSize: 21,
      totalCount: 0,
      page: 1
      // name: 'fans.name'
    }
  },
  mounted () {
    // domecharts
    const myChart = echarts.init(this.$refs.main)
    // console.log(myChart)
    var option = {
      color: ['#3398DB'],
      tooltip: {
        trigger: 'axis',
        axisPointer: { // 
          type: 'shadow'// 'line' | 'shadow'
        }
      },
      grid: {
        left: '3%',
        right: '4%',
        bottom: '3%',
        containLabel: true
      },
      xAxis: [
        {
          type: 'category',
          data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'],
          axisTick: {
            alignWithLabel: true
          }
        }
      ],
      yAxis: [
        {
          type: 'value'
        }
      ],
      series: [
        {
          name: '',
          type: 'bar',
          barWidth: '60%',
          data: [10, 52, 200, 334, 390, 330, 220]
        }
      ]
    }
    // 
    myChart.setOption(option)
  },
  created () {
    this.loadFansList(1)
  },
  methods: {
    handleClick (tab, event) {
      console.log(tab, event)
    },
    loadFansList (page = 1) {
      this.page = page
      getFansList({
        page,
        per_page: this.pageSize
      }).then(res => {
        this.fansList = res.data.data.results
        this.totalCount = res.data.data.total_count
        // console.log(res)
      })
    },
    onPageChange (page) {
      this.loadFansList(page)
    }
  }
}
