<template>
  <div class="settings-container">
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item to="/"></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <!-- / -->
      </div>
      <el-row>
        <el-col :span="15">
          <el-form
           ref="form"
            :model="user"
            :rules="rules"
            label-width="90px"
            class="el-form-item">
            <el-form-item label="">
              {{user.id}}
            </el-form-item>
            <el-form-item label="">
              {{user.mobile}}
            </el-form-item>
            <el-form-item
             label=":"
             prop="name"
             >
              <el-input v-model="user.name"></el-input>
            </el-form-item>
            <el-form-item
             label=":"
             prop="intro"
             >
    <el-input type="textarea" v-model="user.intro"></el-input>
            </el-form-item>
            <el-form-item
             label=":"
             prop="email"
             >
              <el-input v-model="user.email"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button
              type="primary"
              class="save-but"
              @click = "updataUser"
              ></el-button>
            </el-form-item>
          </el-form>
        </el-col>
        <el-col :offset="2" :span="4">
          <el-avatar
            shape="square"
            :size="150"
            fit="cover"
            :src="user.photo"
          ></el-avatar>
          <p @click="$refs.file.click()"></p>
          <input
           type="file"
           ref="file"
           hidden
           @change="onFileChange"
           >
        </el-col>
      </el-row>
    </el-card>
    <!--  -->
    <el-dialog
        title=""
        :visible.sync="dialogVisible"
        :append-to-body=true
        @opened="onDialogOpened"
        @closed="onDialogClosed"
        >
        <div class="preview-image-wrap">
       <img
         :src="previewImage"
         alt=""
         class="preview-image"
         ref="preview-image"
         >
        </div>
        <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false"> </el-button>
            <el-button
             type="primary"
              @click="onUpdataPhoto"
              :loading="onUpdataLoad"
              > </el-button>
        </span>
        </el-dialog>
  </div>
</template>
<script>
import {
  getUserProfile,
  updataUserPhoto,
  updataUserProfile
} from '@/api/user.js'
import 'cropperjs/dist/cropper.css'
import Cropper from 'cropperjs'
import globalBus from '@/utlis/global-bus'
export default {
  name: 'PersonSet',
  data () {
    return {
      user: {
        id: null,
        name: '',
        intro: '',
        mobile: '',
        email: '',
        photo: ''
      },
      dialogVisible: false,
      // 
      previewImage: '',
      cropper: null,
      onUpdataLoad: false,
      rules: {
        name: [
          { required: true, message: '', trigger: 'change' },
          { min: 3, max: 5, message: ' 3  5 ', trigger: 'change' }
        ],
        intro: [
          { required: true, message: '', trigger: 'change' }
        ],
        email: [
          { required: true, message: '', trigger: 'blur' },
          { pattern: /(\w+)@(\w+)\.(\w+)(\.\w+)?/, message: '', trigger: 'change' }
        ]
      }
    }
  },
  created () {
    this.loadUser()
  },
  methods: {
    loadUser () {
      getUserProfile().then(res => {
        this.user = res.data.data
      })
    },
    onFileChange () {
    // 
      const file = this.$refs.file
      const blobData = window.URL.createObjectURL(file.files[0])
      this.previewImage = blobData
      // 
      this.dialogVisible = true
      // change
      this.$refs.file.value = ''
    },
    onDialogOpened () {
      // dom
      const image = this.$refs['preview-image']
      // .
      this.cropper = new Cropper(image, {
        viewMode: 1,
        dragMode: 'none',
        aspectRatio: 1,
        cropBoxResizable: false
      })
    },
    // 
    onDialogClosed () {
      this.cropper.destroy()
    },
    // 
    onUpdataPhoto () {
      this.onUpdataLoad = true
      // 
      this.cropper.getCroppedCanvas().toBlob(file => {
        const fd = new FormData()
        fd.append('photo', file)
        updataUserPhoto(fd).then(res => {
          // console.log(res)
          this.dialogVisible = false
          // 
          // this.user.photo = res.data.data.photo
          // 
          this.user.photo = window.URL.createObjectURL(file)
          this.onUpdataLoad = false
          globalBus.$emit('updata-user', this.user)
        })
      })
    },
    updataUser () {
      updataUserProfile({
        name: this.user.name,
        intro: this.user.intro,
        email: this.user.email
      }).then(res => {
        globalBus.$emit('updata-user', this.user)
        // console.log(res)
        this.$message('')
      })
    }
  }
}
</script>

<style lang="less" scoped>
.preview-image-wrap{
  .preview-image{
    display: block;
    max-width: 100%;
    height: 200px;
  }
}
.save-but{
  margin-top: 20px;
}
</style>
