<template>
   <div class="publish-container">
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item to="/home"></el-breadcrumb-item>
          <el-breadcrumb-item>{{ $route.query.id? '' : ''}}</el-breadcrumb-item>
        </el-breadcrumb>
        <!-- / -->
      </div>
      <el-form  :model="artical"
      :rules="formRules"
       label-width="80px"
       ref="publish-form"
       >
        <el-form-item label="" prop="title">
    <el-input v-model="artical.title"></el-input>
  </el-form-item>
        <el-form-item label="" prop="content">
          <!-- <el-input type="textarea" v-model="artical.content"></el-input> -->
          <el-tiptap
          v-model="artical.content"
           :extensions="extensions"
           height="300"
           placeholder=""
           ></el-tiptap>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="artical.cover.type" clearable>
            <el-radio :label="1"></el-radio>
            <el-radio :label="3"></el-radio>
            <el-radio :label="0"></el-radio>
            <el-radio :label="-1"></el-radio>
          </el-radio-group>
          <!-- <template v-if="artical.cover.type>0">
            <upload-cover
                v-for="(cover, index) in artical.cover.type"
                :key="cover"
                :cover-image = "artical.cover.image[index]"
                @update-cover="onUpdateCover(index, $event)"
            >
            </upload-cover>
          </template> -->
           <template>
            <upload-cover
              v-for="(cover,index) in artical.cover.type"
               :key="cover"
               v-model="artical.cover.images[index]"
              @update-cover="onUpdateCover(index, $event)"
            />
          </template>
        </el-form-item>
        <el-form-item label="" prop="channel_id">
          <el-select v-model="artical.channel_id" placeholder="">
            <el-option
             :label = "channel.name"
             :value="channel.id"
              v-for = "(channel, index) in channels"
               :key = "index"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onPublish(false)"></el-button>
          <el-button @click="onPublish(true)"></el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import { uploadImage } from '@/api/image.js'
import {
  //  extensions
  ElementTiptap,
  Doc,
  Text,
  Paragraph,
  Heading,
  Bold,
  Underline,
  Italic,
  Strike,
  ListItem,
  BulletList,
  OrderedList,
  Image,
  TextColor,
  Iframe,
  FormatClear,
  Fullscreen
} from 'element-tiptap'
// import element-tiptap 
import 'element-tiptap/lib/index.css'
import {
  ConnentChannels,
  addPublishConnent,
  getEditConnent,
  updataConnent
} from '@/api/connent.js'
import UploadCover from './components/upload-cover.vue'
export default {
  name: 'PublishIndex',
  components: {
    'el-tiptap': ElementTiptap,
     UploadCover
  },
  data () {
    return {
      artical: {
        title: '',
        content: '',
        cover: {
          type: 1,
          images: []
        },
        channel_id: ''
      },
      channels: [],
      formRules: {
        title: [
          { required: true, message: '', trigger: 'blur' },
          { min: 5, max: 30, message: ' 5  30 ', trigger: 'blur' }
        ],
        content: [
          // 
          {
            validator (rule, value, callback) {
              if (value === '<p></p>') {
                // 
                callback(new Error(''))
              } else {
                // 
                callback()
              }
            }
          },
          { required: true, message: '', trigger: 'blur' }
        ],
        channel_id: [
          { required: true, message: '' }
        ]
      },
      extensions: [
        new Doc(),
        new Text(),
        new Paragraph(),
        new Heading({ level: 5 }),
        new Bold({ bubble: true }), // 
        new Underline(),
        new Italic(),
        new Strike(),
        new ListItem(),
        new BulletList(),
        new OrderedList(),
        new Image({
        // .base64
          uploadRequest (file) {
            //  Content-Typemultipary/form-data,FormData
            // axiospromisereturnpromise
          // return
            const fd = new FormData()
            fd.append('image', file)
            return uploadImage(fd).then(res => {
              return res.data.data.url
            })
          }
        }
        ),
        new TextColor(),
        new Iframe(),
        new FormatClear(),
        new Fullscreen()
      ]
    }
  },
  created () {
    this.getConnentChannels()
    // 
    if (this.$route.query.id) {
      this.editContent()
    }
  },
  methods: {
    onPublish (draft = false) {
      this.$refs['publish-form'].validate((valid) => {
        // 
        if (!valid) {
          return
        }
        // 
        const connentId = this.$route.query.id
        if (connentId) {
          updataConnent(connentId, this.artical, draft).then(res => {
            this.$message({
              message: `${draft ? '' : ''}`,
              type: 'success'
            })
          })
        } else {
          addPublishConnent(this.artical, draft).then(res => {
            this.$message({
              message: `${draft ? '' : ''}`,
              type: 'success'
            })
          })
        }
        this.$router.push('/connent')
      })
    },
    getConnentChannels () {
      ConnentChannels().then(res => {
        this.channels = res.data.data.channels
      })
    },
    editContent () {
      getEditConnent(this.$route.query.id).then(res => {
        console.log(res)
        this.artical = res.data.data
      })
    },
    onUpdateCover (index,url) {
      // console.log(url)
      this.artical.cover.images[index] = url
    }
  }
}
</script>

