<template>
  <div>
    <el-card class="box-card">
    <div slot="header" class="clearfix">
  <!--  -->
  <el-breadcrumb separator-class="el-icon-arrow-right">
  <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
  <el-breadcrumb-item></el-breadcrumb-item>
</el-breadcrumb>
    </div>
  <!--  -->
   <template>
    <el-table
      class="table-list"
      :data="articals"
      style="width: 100%">
      <el-table-column
        prop="title"
        label=""
        >
      </el-table-column>
      <el-table-column
        prop="total_comment_count"
        label=""
        >
      </el-table-column>
      <el-table-column
        prop="fans_comment_count"
        label="">
      </el-table-column>
      <el-table-column
        prop="address"
        label="">
        <template slot-scope="scope">
            {{scope.row.comment_status ? '' : ''}}
        </template>
      </el-table-column>
      <el-table-column
        prop="address"
        label="">
        <template slot-scope="scope">
           <el-switch
            v-model="scope.row.comment_status"
            active-color="#13ce66"
            inactive-color="#ff4949"
            :disabled="scope.row.statusDisabled"
            @change="onStatusChange(scope.row)"
            >
            </el-switch>
        </template>
      </el-table-column>
    </el-table>
  </template>
  <!--  -->
    <!--current-page  -->
    <!--page-sizes -->
    <!--page-size  -->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page.sync= page
      :page-sizes="[10, 20, 30, 40]"
      :page-size.sync="pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="totalCount">
    </el-pagination>
    </el-card>
  </div>
</template>

<script>
import {
  connent,
  updataCommentStatus
} from '@/api/connent'
export default {
  name: 'CommentIndex',
  data () {
    return {
      tableData: [{
        date: '2016-05-02',
        name: '',
        address: ' 1518 '
      }, {
        date: '2016-05-04',
        name: '',
        address: ' 1517 '
      }, {
        date: '2016-05-01',
        name: '',
        address: ' 1519 '
      }, {
        date: '2016-05-03',
        name: '',
        address: ' 1516 '
      }],
      articals: [],
      totalCount: 0,
      pageSize: 10,
      page: 1
    }
  },
  created () {
    this.loadArticals()
  },
  methods: {
    handleSizeChange () {
      this.loadArticals(1)
    },
    handleCurrentChange (page) {
      this.loadArticals(page)
    },
    loadArticals (page = 1) {
      this.page = page
      connent({
        response_type: 'comment',
        page,
        per_page: this.pageSize
      }).then(res => {
        // console.log(res)
        const results = res.data.data.results
        results.forEach(articals => {
          articals.statusDisabled = false
        })
        this.articals = results
        this.totalCount = res.data.data.total_count
      })
    },
    onStatusChange (articals) {
      articals.statusDisabled = true
      updataCommentStatus(articals.id.toString(), articals.comment_status).then(res => {
        articals.statusDisabled = false
        this.$message({
          type: 'success',
          message: articals.comment_status ? '' : ''
        })
      })
    }
  }
}
</script>

<style>
.table-list{
    margin-bottom: 30px;
}
</style>
